@import '../../../../scss/styles';

$block: url-input;
$blockLabel: url-input__label;
$blockBorder: url-input__border;

.#{$block} {
  display: flex;
  position: relative;
  width: 100%;
  max-width: 210px;
  &__label {
      position: absolute;
      top: -5px;
      left: 0;
      font-size: 14px;
      color: $color-alert;
      font-weight: 500;
      transform-origin: 0 0;
      transition: all 0.2s ease;
  }

  &__border {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: $color-alert;
    transform: scaleX(0);
    transform-origin: 0 0;
    transition: all 0.15s ease;
  }
}

 input {
  -webkit-appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  padding: 8px 0;
  height: 36px;
  font-size: 16px;
  font-weight: 500;
  border-bottom: 2px solid $color-light-blue;
  background: none;
  border-radius: 0;
  color: $color-dark-grey;
  transition: all 0.15s ease;
}

input:not(:placeholder-shown) + span {
  color: $color-alert;
  transform: translateY(-26px) scale(0.75);
}

input:focus {
  background: none;
  outline: none;
}

input:focus + span {
  transform: translateY(-26px) scale(0.75);
}
input:focus + span + .border {
  transform: scaleX(1);
}

:export {
  block: $block;
  blockLabel: $blockLabel;
  blockBorder: $blockBorder;
}